import React,{useEffect} from 'react';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { fetchCart } from "../../../store/actions/cart";
import styled from 'styled-components';
import { RiShoppingCartFill } from 'react-icons/ri';
import { RiAccountPinCircleFill } from 'react-icons/ri';

const Ul = styled.ul`
  @media (max-width: 830px) {
    flex-flow: column nowrap;
    background-color: #1F1D29;
    position: fixed;
    transform: ${({ open }) => open ? 'translateX(0)' : 'translateX(100%)'};
    top: 0;
    right: 0;
    height: 100vh;
    width: 250px;
    padding-top: 3.5rem;
    z-index: 1;
    transition: transform 0.3s ease-in-out;
    li {
      color: #fff;
      margin: 10px;
    }
  }
`;


const RightNav = ({ open, isAuthenticated, fetchCart, cart }) => {
  useEffect(() => {
    fetchCart()
  },[fetchCart])
  return (
        <Ul open={open}>
        <li><Link to="/gamingpc">gaming pc</Link></li>
        <li><Link to="/pccomponents">Pc Components</Link></li>
        <li><Link to="/custom">Build Your PC</Link></li>
        <li><Link to="/aboutus">About us</Link></li>
        {
                    isAuthenticated ?
                    <div style={{ display: 'flex',flexdirection: 'row', marginTop: '10px'}}>
                    <Link to='/order'><p><RiShoppingCartFill size={30} style={{ color: 'white', margin: '10px'}} />{cart !== null ? cart.order_items.length : 0 }</p></Link>
                    <Link to="/account"><RiAccountPinCircleFill size={30} style={{ color: 'white', margin: '10px'}} /></Link>
                    </div>
                    :
                    <li><Link to="/login">login</Link></li>
        }
        </Ul>
  )
}

const mapStateToProps = state => {
    return {
      isAuthenticated: state.auth.isAuthenticated,
      cart: state.cart.shoppingCart
    }
  }
  const mapDispatchToProps = dispatch => {
    return {
      fetchCart: () => dispatch(fetchCart())
    }
  }
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(RightNav))