import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import axios from 'axios'
import { endpoint } from "../../constant";
import { BiRupee } from "react-icons/bi";
import './styles.css'

export class Motherb extends Component {
    state = {
        partsdata: [],
        select: 'M',
    }

    componentDidMount(){
        axios.get(`${endpoint}/pccomponent/`)
        .then(res => {
            this.setState({
                partsdata: res.data
            })
        })
    }
    render() {
        let motherbdata = this.state.partsdata.filter((partsdata) => {
            return partsdata.category.includes(this.state.select)
        })
        return (
            <div className='Product-div'>
            {
               motherbdata &&
               motherbdata.map( motherbdata =>{
                   return(
                        <div className='product-card' key={motherbdata.id}>
                        <Link to={`/produuctdetail/${motherbdata.id}`}>
                        <img src={motherbdata.image} alt='processorimages'></img>
                        </Link>
                        <div className='product-card-info'>
                            <h2>{motherbdata.title}</h2>
                            <p><BiRupee size='30px' />{motherbdata.price}</p>
                        </div>
                        </div>
                   )                       
                })
          }

        </div>
        )
    }
}

export default Motherb
